@import '~antd/dist/antd.less';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}
body {
  font-family: 'Microsoft YaHei';
  font-size: 14px;
}

a {
  text-decoration: none;
  color: #1890ff;
}

img {
  border: none;
  vertical-align: middle;
  &[src=''],
  &:not([src]) {
    opacity: 0;
  }
}

table {
  border-collapse: collapse;
  table-layout: fixed;
}

input,
textarea {
  outline: none;
  border: none;
}

